<template>
  <div id="c-ars">
    <!-- <van-button
      size="large"
      color="linear-gradient(to right, #4bb0ff, #6149f6)"
      @touchstart.prevent="startRecorder"
      @touchend="endRecorder"
      @mousedown.prevent="startRecorder"
      @mouseup.prevent="endRecorder"
    ></van-button>-->
    <div class="info">
      <p></p>
      <p>~</p>
      <p></p>
    </div>
    <button
      id="microphone"
      @touchstart.prevent="startRecorder"
      @touchend="endRecorder"
      @mousedown.prevent="startRecorder"
      @mouseup.prevent="endRecorder"
    ></button>
    <!--  -->
    <!-- <div class="pd recpower">
      <div style="height:40px;width:300px;background:#999;position:relative;">
        <div class="recpowerx" style="height:40px;background:#0B1;position:absolute;"></div>
        <div class="recpowert" style="padding-left:50px; line-height:40px; position: relative;"></div>
      </div>
    </div>
    <div class="pd waveBox">
      <div
        style="height:100px;width:300px;border:1px solid #ccc;box-sizing: border-box;display:inline-block"
        class="recwave"
      ></div>
    </div>
    <button onclick="recPlay()"></button>-->
  </div>
</template>

<script>
import Recorder from 'recorder-core'
import 'recorder-core/src/engine/wav'
import 'recorder-core/recorder.wav.min.js'
import 'recorder-core/src/extensions/waveview.js'

export default {
  data () {
    return {
      userId: '',
      client_id: 'lOQ5dD9hdxO4fYSpZC5ag9k7',
      client_secret: 'cEfW9QHZsnD0wivPHqwU6FDELSG4WjUu',
      baiduToken: null,
      rec: null,
      // wave: null,
      recBlob: null,
      dialogInt: null,
      websock: null,
      wsuri: 'ws://' + 'localhost:1209' + '/websocket/'
    }
  },
  mounted () {
    this.userId = window.sessionStorage.getItem('userId')
    this.getBaiduToken();
    this.initWebSocket(); //websocket
  },
  destroyed () {
    this.recClose(); //
    // this.websock.close() //websocket
  },
  methods: {
    //   Token
    getBaiduToken: async function () {
      const { data: res } = await this.$http.post(`/getBaidu/oauth/2.0/token?grant_type=client_credentials&client_id=${this.client_id}&client_secret=${this.client_secret}`)
      this.baiduToken = res.access_token;
    },
    upload: function () {
      var self = this;
      var reader = new FileReader();
      reader.onloadend = async function () {
        const { data: res } = await self.$http.post('/vop.baidu.com/pro_api',
          {
            "format": "wav",
            "rate": 16000,
            "dev_pid": 80001,
            "channel": 1,
            "token": self.baiduToken,
            "cuid": "baidu_workshop",
            "len": self.recBlob.size,
            "speech": (/.+;\s*base64\s*,\s*(.+)$/i.exec(reader.result) || [])[1], // xxx base64FILE_CONTENT
          })
        window.console.log(res);
        if (res.err_no == '0') {
          self.websocketsend(res.result);
        } else {
          self.$toast.fail('~')
          self.websocketsend('[cant Recognition]');
        }
      };
      reader.readAsDataURL(this.recBlob);





    },
    startRecorder: function () {
      this.recStart();
    },
    endRecorder: function () {
      this.recStop();
    },

    /**open**/
    recOpen: function () {//
      this.rec = null;
      // this.wave = null;
      this.recBlob = null;
      var self = this;
      var newRec = Recorder({
        type: "wav", sampleRate: 16000, bitRate: 16 //mp3hzkbpstypewavwav.js
        , onProcess: function (buffers, powerLevel, bufferDuration, bufferSampleRate) {
          //112
          document.querySelector(".recpowerx").style.width = powerLevel + "%";
          document.querySelector(".recpowert").innerText = bufferDuration + " / " + powerLevel;

          //
          self.wave.input(buffers[buffers.length - 1], powerLevel, bufferSampleRate);
        }
      });
      this.createDelayDialog(); //UCdemo
      newRec.open(function () {//
        self.dialogCancel(); //

        self.rec = newRec;

        //
        self.wave = Recorder.FrequencyHistogramView({ elem: ".recwave" });

        self.reclog("", 2);
      }, function (msg, isUserNotAllow) {//
        self.dialogCancel(); //
        self.reclog((isUserNotAllow ? "UserNotAllow" : "") + "" + msg, 1);
      });

      window.waitDialogClick = function () {
        self.dialogCancel();
        self.reclog("<span style='color:#f00'></span>", 1);
      };
    },

    /****/
    recClose: function () {
      if (this.rec) {
        this.rec.close();
        this.reclog("");
      } else {
        this.reclog("", 1);
      }
    },

    /****/
    recStart: function () {//startstop
      if(!this.rec){
        this.recOpen(); //
      }

      if (this.rec && Recorder.IsOpen()) {
        this.recBlob = null;
        this.rec.start();
        this.reclog("...");
      } else {
        this.reclog("", 1);
      }
    },

    /****/
    recStop: function () {
      if (!(this.rec && Recorder.IsOpen())) {
        this.reclog("", 1);
        return;
      }
      var self = this;
      this.rec.stop(function (blob, duration) {
        window.console.log(blob, (window.URL || window.webkitURL).createObjectURL(blob), ":" + duration + "ms");

        self.recBlob = blob;

        // 
        self.upload();



        self.reclog("wav" + duration + "ms " + blob.size + "", 2);
      }, function (msg) {
        self.reclog(":" + msg, 1);
      });
    },
    /****/
    recPlay: function () {
      if (!this.recBlob) {
        this.reclog("", 1);
        return;
      }
      var cls = ("a" + Math.random()).replace(".", "");
      this.reclog(': <span class="' + cls + '"></span>');
      var audio = document.createElement("audio");
      audio.controls = true;
      document.querySelector("." + cls).appendChild(audio);
      //URLrevokeObjectURL
      audio.src = (window.URL || window.webkitURL).createObjectURL(this.recBlob);
      audio.play();

      setTimeout(function () {
        (window.URL || window.webkitURL).revokeObjectURL(audio.src);
      }, 5000);
    },





    //recOpenUC
    showDialog: function () {
      if (!/mobile/i.test(navigator.userAgent)) {
        return;//
      }
      this.dialogCancel();

      //
      var div = document.createElement("div");
      document.body.appendChild(div);
      div.innerHTML = (''
        + '<div class="waitDialog" style="z-index:99999;width:100%;height:100%;top:0;left:0;position:fixed;background:rgba(0,0,0,0.3);">'
        + '<div style="display:flex;height:100%;align-items:center;">'
        + '<div style="flex:1;"></div>'
        + '<div style="width:240px;background:#fff;padding:15px 20px;border-radius: 10px;">'
        + '<div style="padding-bottom:10px;">~</div>'
        + '<div style="text-align:center;"><a onclick="waitDialogClick()" style="color:#0B1"></a></div>'
        + '</div>'
        + '<div style="flex:1;"></div>'
        + '</div>'
        + '</div>');
    },
    createDelayDialog: function () {
      var self = this;
      this.dialogInt = setTimeout(function () {//8openopen
        self.showDialog();
      }, 8000);
    },
    dialogCancel: function () {
      clearTimeout(this.dialogInt);

      //
      var elems = document.querySelectorAll(".waitDialog");
      for (var i = 0; i < elems.length; i++) {
        elems[i].parentNode.removeChild(elems[i]);
      }
    },
    //recOpenEnd

    reclog: function (s, color) {
      var now = new Date();
      var t = ("0" + now.getHours()).substr(-2)
        + ":" + ("0" + now.getMinutes()).substr(-2)
        + ":" + ("0" + now.getSeconds()).substr(-2);
      // var div = document.createElement("div");
      // var elem = document.querySelector(".reclog");
      // elem.insertBefore(div, elem.firstChild);
      // div.innerHTML = '<div style="color:' + (!color ? "" : color == 1 ? "red" : color == 2 ? "#0b1" : color) + '">[' + t + ']' + s + '</div>';
      window.console.log((!color ? "" : color == 1 ? "error" : color == 2 ? "" : color) + '[' + t + ']' + s);

    },




    // websocket
    initWebSocket () { //weosocket
      const wsuri = this.wsuri;
      this.websock = new WebSocket(wsuri + this.userId);
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onopen = this.websocketonopen;
      this.websock.onerror = this.websocketonerror;
      this.websock.onclose = this.websocketclose;
    },
    websocketonopen () { //send
      let actions = {
        "to": "show" + this.userId,
        "text":"[control]Opened" 
        };
      this.websocketsend(JSON.stringify(actions));
    },
    websocketonerror () {//
      this.initWebSocket();
    },
    websocketonmessage (e) { //
      // const redata = JSON.parse(e.data);
      window.console.log(e)
    },
    websocketsend (Data) {//
      this.websock.send(Data);
    },
    websocketclose (e) {  //
      window.console.log('', e);
    }
  }
}
</script>

<style lang="less" scoped>
.info {
  width: 80%;
  margin: 20px auto;
  color: #555;
  p {
    margin-top: 10px;
  }
}
