  background: #4bb0ff;
  color: #fff;
  border: 2px solid #ccc;
  position: absolute;
  bottom: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  display: block;
  transition: all 0.5s ease;
  &:hover,
  &:active {
    color: black;
  }
}
</style>